import uniGoodsNav from '@/components/uni-goods-nav/uni-goods-nav.vue'
import uniIcons from "@/components/uni-icons/uni-icons.vue"
export default {
	data() {
		return {
			id:'',
			sessionId:'',
			myData:{},
			options:[{
				icon: 'cart',
				text: '购物车',
				info: 0
			}],
			buttonGroup: [{
				text: '加入购物车',
				backgroundColor: '#ff0000',
				color: '#fff'
			},
			{
				text: '立即购买',
				backgroundColor: '#ffa200',
				color: '#fff'
			}],
			buyQty:1,
			url:'',
			flag:''
		}
	},
	onLoad(option) {
		this.id = option.id
		this.sessionId = option.sessionId
		this.url = option.url
		this.init()
	},
	methods: {
		// 页面渲染
		init(){
			uni.request({
				url:'https://www.nssnail.com/wx/getGoodsById',
				data:{
					sessionId:this.sessionId,
					id:this.id
				},
				success: (res) => {
					if(res.data.stat == 1){
						this.myData = res.data.data
					}else if(res.data.stat == 3 || res.data.stat == 4){
						uni.showModal({
						    title: '提示',
						    content: '登录过期，请重新登录',
						    success: function (res) {
						        if (res.confirm) {
						            uni.navigateTo({
						            	url:'../myInit/myInit'
						            })
						        }
						    }
						});
					}
				}
			})
		},
		
		// 点击跳转至购物车
		onClick(e){
			uni.navigateTo({
				url:'../record/buyCar'
			})
		},
		
		// 
		buttonClick(e){
			if(e.index == 0){
				this.$refs.popup.open()
				this.flag = 1
				this.buyQty = 1
			}else{
				this.flag = 2
				this.$refs.popup.open()
				this.buyQty = 1
			}
			
		},
		
		// 关闭弹窗
		closePop(){
			this.$refs.popup.close()
		},
		
		// 数量
		reduce(){
			if(this.buyQty > 1){
				this.buyQty --
			}else{
				uni.showToast({
					title:'数量低于范围~',
					icon:'none'
				})
			}
		},
		
		// 增加
		add(){
			if(this.buyQty < this.myData.qty){
				this.buyQty ++
			}else{
				uni.showToast({
					title:'库存不够了~',
					icon:'none'
				})
				return
			}
		},
		
		// 加入购物车
		sure(){
			//当flag为1时调用加入购物车接口
			if(this.flag == 1){
				this.addCart()
			}else{
				this.buyNow()
			}
		},
		
		// 加入购物车
		addCart(){
			var price = parseInt(this.buyQty) * parseInt(this.myData.price)
			uni.request({
				url:'https://www.nssnail.com/wx/addCart',
				method:'POST',
				data:{
					goodId:this.myData.id,
					name:this.myData.name,
					qty:this.buyQty,
					price:price,
					sessionId:this.sessionId
				},
				success: (res) => {
					console.log(res,'res')
					if(res.data.stat == 1){
						uni.showToast({
							title:'添加成功',
							icon:'success',
							duration:2000
						})
						this.$refs.popup.close()
					}else if(res.data.stat == 3){
						uni.showModal({
						    title: '提示',
						    content: '登录过期，请重新登录',
						    success: function (res) {
						        if (res.confirm) {
						            uni.navigateTo({
						            	url:'../myInit/myInit'
						            })
						        }
						    }
						});
						this.$refs.popup.close()
					}else if(res.data.stat == 0){
						uni.showToast({
							title:res.data.message,
							icon:'none',
							duration:3000
						})
						this.$refs.popup.close()
					}
				}
			})
		},
		
		// 立即购买
		buyNow(){
			var price = parseInt(this.buyQty) * parseInt(this.myData.price)
			uni.request({
				url:'https://www.nssnail.com/wx/createOrder',
				method:'POST',
				data:{
					goodId:this.myData.id,
					qty:this.buyQty,
					price:price,
					sessionId:this.sessionId
				},
				success: (res) => {
					console.log(res,'res')
					if(res.data.stat == 1){
						uni.showToast({
							title:'添加成功',
							icon:'success',
							duration:2000,
							success:()=>{
								setTimeout(function(){
									uni.navigateTo({
										url:'../record/record'
									})
								},3000);
							}
						})
					}else if(res.data.stat == 3){
						uni.showModal({
						    title: '提示',
						    content: '登录过期，请重新登录',
						    success: function (res) {
						        if (res.confirm) {
						            uni.navigateTo({
						            	url:'../myInit/myInit'
						            })
						        }
						    }
						});
						this.$refs.popup.close()
					}else if(res.data.stat == 0){
						uni.showToast({
							title:res.data.message,
							icon:'none',
							duration:3000
						})
						this.$refs.popup.close()
					}
				}
			})
		}
	},
	components:{
		uniGoodsNav,
		uniIcons
	}
}
